const getReference = () => {
  //you can put any unique reference implementation code here
  let text = "";
  let possible =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-.=";

  for (let i = 0; i < 15; i++)
    text += possible.charAt(Math.floor(Math.random() * possible.length));

  return text;
};
const { token, shecludedId } = JSON.parse(sessionStorage.getItem("userData"));

useEffect(() => {
  setState({
    ...state,
    fetchingData: true,
  });
  //Handling Card Verification
  if (state.defaultCard) {
    addCardDetails();
  }

  const { page, size } = state;
  let reqBody = {
    pageNumber: page,
    pageSize: size,
    shecludedId,
  };
  axios
    .all([
      axios({
        url: `${profileUrl}`,
        method: "get",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        timeout: timeOut,
      }),
      axios({
        url: `${allTransactions}`,
        method: "post",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        timeout: timeOut,
        data: reqBody,
      }),
      axios({
        url: `${getNextLoanDetails}`,
        method: "get",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        timeout: timeOut,
      }),
    ])
    .then(
      axios.spread((result1, result2, result3) => {
        setState({
          ...state,
          fetchingData: false,
        });
        if (
          result1.data.respCode === "Successful" &&
          result2.data.respCode === "00"
        ) {
          const {
            amountRemaining,
            amountPaid,
            nextPaymentDate,
            amountLoaned,
          } = result3.data.respBody;
          const { email, cards } = result1.data.respBody;
          setState({
            ...state,
            fetchedRequestBody: result1.data.respBody,
            email,
            cardDetails: cards,
            transactions: result2.data.respBody.content,
            amountRemaining,
            amountPaid,
            nextPaymentDate,
            amountLoaned,
          });
        } else {
          setState({
            ...state,
            errorMessage: result2.data.respDescription,
          });
        }
      })
    )
    .catch((err) => {
      setState({
        ...state,
        fetchingData: false,
      });
      if (!err.response) {
        setState({
          ...state,
          loginErrorMessage: "Network Error",
        });
      } else {
        if (err.response.status === 400 || err.response.status === 401) {
          setState({
            ...state,
            errorMessage: "An error occured, please try again.",
          });
        } else {
          setState({
            ...state,
            errorMessage: err,
          });
        }
      }
    });
}, [state.defaultCard]);

const addCardDetails = () => {
  dismissPaymentModal.current.click();
  setState({
    ...state,
    fetchingData: true,
  });
  let reqBody = {
    ...state.fetchedRequestBody,
    idFile: "",
    photo: "",
    pitchVideo: "",
    bankStatement: "",
    cards: [
      {
        authDetails: "",
        cardToken: state.cardToken,
        createdAt: new Date().toISOString(),
        customerDetails: "",
        customerToken: state.customerToken,
        defaultCard: true,
        delFlag: "",
        id: 0,
        updatedAt: new Date().toISOString(),
        version: 0,
      },
    ],
  };
  axios({
    method: "put",
    url: `${profileUrl}`,
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`,
    },
    data: reqBody,
    timeout: timeOut,
  })
    .then((result) => {
      setState({
        ...state,
        fetchingData: false,
      });
      if (result.data.respCode === "Successful") {
        window.location.reload();
        swal(
          "Successful Operation",
          "Your profile information have been updated successfully.",
          "success"
        );
      } else {
        setState({
          ...state,
          fetchingData: false,
          errorMessage: result.data.respDescription,
        });
      }
    })
    .catch((err) => {
      setState({
        ...state,
        fetchingData: false,
        errorMessage: err,
      });
    });
};


